(function(mediaid){
	mediaid.fileid = function(extension){
		switch(extension){
			case '.mp4':
			case '.mov':
			case '.m4v':
			case '.m4a':
			case '.m4p':
			case '.m4b':
			case '.m4r':
				return 'qtff';
			case '.avi':
			case '.wav':
				return 'riff';
			case '.wmv':
			case '.wma':
			case '.asf':
				return 'asf';
			default:
				return null;
		}
	};
}(mediaid));
